<script>


export default {
  name: "Signup",
  data() {
    return {
        progressWind: false,
        showNext: false,
      img: null,
      camera: null,
      deviceId: null,
      pictureTaken: false,
      resultmodal: false,
      output: '',
     devices: [],
     canvas: null,
     screenshotFormat: '',
     progression: 0
    };
  },
  
  
  methods: {

      extracttext(){
          console.log('starting now..')
      Tesseract.recognize(this.img)
    .progress((progress)=> {

           this.progression = (progress.progress * 100).toFixed(2)
           this.progressWind = true
        
        }).then((result)=> {
        this.output = result.text
        this.progressWind = false
        this.pictureTaken = false
        this.resultmodal = true
        this.showNext = true
 
    })
        
      },

      onCapture(){
          this.img = this.capture(), 
          this.pictureTaken = true
      },

  capture() {
      return this.getCanvas().toDataURL(this.screenshotFormat)
    },
    getCanvas() {
      let video = this.$refs.webvideo;
      if (!this.ctx) {
        let canvas = document.createElement("canvas");
        canvas.height = video.videoHeight;
        canvas.width = video.videoWidth;
        this.canvas = canvas;

        this.ctx = canvas.getContext("2d");
      }

      const { ctx, canvas } = this;
      ctx.drawImage(video, 0, 0, canvas.width, canvas.height);

      return canvas;
    }

  },
  mounted(){
      navigator.mediaDevices.getUserMedia({ video: { facingMode: { exact: "environment" }}}).then(stream => {
            this.$refs.webvideo.srcObject = stream
        });
  }
};
</script>

